<template>
  <aside :class="['main-sidebar', 'col-12', 'col-md-3', 'col-lg-2', 'px-0', sidebarVisible ? 'open' : '']">
    <div class="main-navbar">
      <nav class="navbar align-items-stretch navbar-light bg-white flex-md-nowrap border-bottom p-0">
        <a
          class="navbar-brand w-100 mr-0"
          href="#"
          style="line-height: 25px;"
        >
          <div class="d-table m-auto">
            <img
              id="main-logo"
              class="d-inline-block align-top mr-1"
              style="max-width: 25px;"
              src="@/assets/images/shards-dashboards-logo.svg"
              alt="НИП"
            >
            <span
              v-if="!hideLogoText"
              class="d-none d-md-inline ml-1"
            >НИП</span>
          </div>
        </a>
        <a
          class="toggle-sidebar d-sm-inline d-md-none d-lg-none"
          @click="handleToggleSidebar"
        >
          <i class="material-icons">&#xE5C4;</i>
        </a>
      </nav>
    </div>
    <div
      v-if="items"
      class="nav-wrapper"
    >
      <div
        v-for="(nav, navIdx) in items"
        :key="navIdx"
      >
        <h6
          v-if="nav.title"
          class="main-sidebar__nav-title"
        >
          {{ nav.title }}
        </h6>
        <d-nav
          v-if="typeof nav.items !== 'undefined' && nav.items.length"
          class="nav--no-borders flex-column"
        >
          <li
            v-for="(item, navItemIdx) in nav.items"
            :key="navItemIdx"
            class="nav-item dropdown"
          >
            <d-link
              v-d-toggle="`snc-${navIdx}-${navItemIdx}`"
              :class="['nav-link', item.items && item.items.length ? 'dropdown-toggle' : '']"
              :to="item.to"
            >
              <div
                v-if="item.htmlBefore"
                class="item-icon-wrapper"
                v-html="item.htmlBefore"
              />
              <span v-if="item.title">{{ item.title }}</span>
              <div
                v-if="item.htmlAfter"
                class="item-icon-wrapper"
                v-html="item.htmlAfter"
              />
            </d-link>
            <d-collapse
              v-if="item.items && item.items.length"
              :id="`snc-${navIdx}-${navItemIdx}`"
              class="dropdown-menu dropdown-menu-small"
              accordion="sidebar-items-accordion"
            >
              <d-dropdown-item
                v-for="(subItem, subItemIdx) in item.items"
                :key="subItemIdx"
                :href="subItem.href"
                :to="subItem.to"
              >
                {{ subItem.title }}
              </d-dropdown-item>
            </d-collapse>
          </li>
        </d-nav>
      </div>
    </div>
  </aside>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'MainSidebar',
  props: {
    /**
      * Whether to hide the logo text, or not.
      */
    hideLogoText: {
      type: Boolean
    },
    /**
     * The menu items.
     */
    items: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      sidebarVisible: false
    }
  },
  computed: {
    ...mapState(['isAuth'])
  },
  created () {
    this.$eventHub.$on('toggle-sidebar', this.handleToggleSidebar)
  },
  beforeDestroy () {
    this.$eventHub.$off('toggle-sidebar')
  },
  methods: {
    handleToggleSidebar () {
      this.sidebarVisible = !this.sidebarVisible
    }
  }
}
</script>

<style lang="scss">
  .main-sidebar {
    .item-icon-wrapper {
      display: inline-block;
    }
    .dropdown-menu {
      display: block;
    }
  }
</style>
